// window.$ = window.jQuery.ÁÆÄÂåñ‰ª£Á†Å
window.$ = window.jQuery = function(selectorOrArrayOrTemplate) {
  let elements; //ÂèòÈáè‰ΩúÁî®ÂüüÊèêÂçáÔºåÂõ†‰∏∫Âú®ifÈáåÂ£∞ÊòéÁöÑËØùÂ∞±Âè™ËÉΩ‰ΩúÁî®Âú®ifÂÜÖÈÉ®‰∫Ü
  if (typeof selectorOrArrayOrTemplate === "string") {
    if (selectorOrArrayOrTemplate[0] === "<") {
      //ÂàõÂª∫divÔºö
      elements = [createElement(selectorOrArrayOrTemplate)];
    } else {
      //Êü•ÊâædivÔºö
      //elementsÊòØ‰∏Ä‰∏™Êï∞ÁªÑÔºåÂåÖÂê´Ëé∑ÂèñÂà∞ÁöÑÊâÄÊúâselectorÂÖÉÁ¥†
      elements = document.querySelectorAll(selectorOrArrayOrTemplate);
    }
  } else if (selectorOrArrayOrTemplate instanceof Array) {
    elements = selectorOrArrayOrTemplate;
  }

  function createElement(string) {
    const container = document.createElement("template");
    container.innerHTML = string.trim();
    return container.content.firstChild;
  }

  //jQueryËé∑ÂèñÂÖÉÁ¥†selectorOrArrayOrTemplate,‰ΩÜÊòØÂπ∂‰∏çËÉΩËøîÂõûËøô‰∏™ÂÖÉÁ¥†ÔºåÂè™ËÉΩËøîÂõû‰∏Ä‰∏™ÂØπË±°ÔºåÈÄöËøáÂØπË±°ÈáåÁöÑÂáΩÊï∞ÔºàÊñπÊ≥ïÔºâÂèØ‰ª•ÊìçÊéßËøô‰∏™ÂÖÉÁ¥†
  //‰∏ãËæπÊòØjQueryËøîÂõûÁöÑÂØπË±°ÔºåÂØπË±°ÈáåÊúâÂ±ûÊÄßaddClassÁ≠â,Ëøô‰∫õÂ±ûÊÄßÊòØÊñπÊ≥ïÔºàÂç≥ÂáΩÊï∞Ôºâ
  return {
    jquery: true,
    elements: elements,
    // Ëé∑ÂèñÂà∞Êñ∞ÂàõÂª∫ÁöÑÂÖÉÁ¥†Ôºö
    get(index) {
      return elements[index];
    },
    // ÊääÊñ∞ÂàõÂª∫ÁöÑÂÖÉÁ¥†ÊîæÂà∞È°µÈù¢Èáå1Ôºö
    appendTo(node) {
      if (node instanceof Element) {
        // ÈÅçÂéÜ elementsÔºåÂØπÊØè‰∏™ el ËøõË°å node.appendChild Êìç‰Ωú
        this.each(el => node.appendChild(el));
      } else if (node.jquery === true) {
        // ÈÅçÂéÜ elementsÔºåÂØπÊØè‰∏™ el ËøõË°å node.get(0).appendChild(el))  Êìç‰Ωú
        this.each(el => node.get(0).appendChild(el));
      }
    },
    // ÊääÂÖÉÁ¥†ÊîæÂà∞È°µÈù¢Èáå2Ôºö
    append(children) {
      // Â¶ÇÊûúchildrenÊòØ‰∏Ä‰∏™ÂÖÉÁ¥†
      if (children instanceof Element) {
        // Â∞±Ëé∑ÂèñÂà∞ÂΩìÂâçapiÁöÑÁ¨¨0‰∏™ÊèíÂÖ•childrenÔºö
        this.get(0).appendChild(children);
      }
      // Â¶ÇÊûúchildrenÊòØÂ§ö‰∏™ÂÖÉÁ¥†Ôºö
      else if (children instanceof HTMLCollection) {
        //ÈÅçÂéÜchildrenÔºö
        for (let i = 0; i < children.length; i++) {
          // ÊäächildrenÁöÑÁ¨¨iÈ°πÊîæÂà∞ÂΩìÂâçapiÁöÑÁ¨¨‰∏Ä‰∏™ÂÖÉÁ¥†ÈáåÔºö
          this.get(0).appendChild(children[i]);
        }
      }
      // Â¶ÇÊûúchildrenÊòØ‰∏Ä‰∏™jqueryÂØπË±°Ôºö
      else if (children.jquery === true) {
        children.each(node => this.get(0).appendChild(node));
      }
    },
    //Âú®‰∏Ä‰∏™divÈáåÊâæ‰∏Ä‰∏™ÂÖÉÁ¥†
    find(selector) {
      let array = [];
      //ÈÅçÂéÜelementsÊï∞ÁªÑ
      for (let i = 0; i < elements.length; i++) {
        //Âú®elementsÊï∞ÁªÑÁöÑÊØè‰∏™ÂÖÉÁ¥†ÈáåÊâæÂà∞selectorÂØπÂ∫îÁöÑÊâÄÊúâÂÖÉÁ¥†,Ëøô‰∫õÂÖÉÁ¥†ÊòØ‰∏Ä‰∏™‰º™Êï∞ÁªÑÔºõ
        // Áî®Array.fromÂèòÊàêÁúüÊ≠£ÁöÑÊï∞ÁªÑÔºåÂπ∂ËµãÂÄºÁªôelements2
        const elements2 = Array.from(elements[i].querySelectorAll(selector));
        //ÂÜçÊääÁ©∫Êï∞ÁªÑËøûÊé•‰º™Êï∞ÁªÑÔºåÂêàËµ∑Êù•ÁªÑÊàêÊñ∞ÁöÑÊï∞ÁªÑÔºåËøô‰∏™Êñ∞Êï∞ÁªÑÈáåÂ∞±ÊòØÊâæÂà∞ÁöÑÊâÄÊúâselectorÂÖÉÁ¥†
        array = array.concat(elements2);
      }
      //ËøîÂõûËøô‰∏™Êñ∞Êï∞ÁªÑ:
      // return array; //‰ΩÜÊ≠§Êó∂Â∞±‰∏çËÉΩËøõË°åÈìæÂºèÊìç‰Ωú‰∫ÜÔºåÂõ†‰∏∫ËøîÂõûÁöÑarrayÊòØ‰∏Ä‰∏™Á∫ØÊï∞ÁªÑ
      //return this‰πü‰∏çË°åÔºåÂõ†‰∏∫Ê≠§Êó∂ÁöÑthisÊåáÁöÑÊòØË∞ÉÁî®findÂáΩÊï∞ÁöÑÂØπË±°Ôºõ
      //Ëøô‰∏™Êó∂ÂÄôÁªßÁª≠ÈìæÂºèÊìç‰ΩúÊìç‰ΩúÁöÑÊòØ‰πãÂâçÁöÑÂØπË±°ÔºåËÄå‰∏çÊòØÁé∞Âú®find(selector)‰πãÂêéÁöÑÂØπË±°

      array.oldApi = this; //thisÂ∞±ÊòØapi
      // const newApi = jQuery(array); //ÊûÑÂª∫‰∏Ä‰∏™Êñ∞ÁöÑapiÂØπË±°
      // return newApi; //Êü•ÊâæÂÖÉÁ¥†ÂêéËøîÂõûÊñ∞ÁöÑapiÂØπË±°Ôºå‰ªéËÄåÂèØ‰ª•ÁªßÁª≠ÈìæÂºèÊìç‰Ωú
      return jQuery(array); //‰∏äËæπ‰∏§Ë°å‰ª£Á†ÅÂèØ‰ª•ÁÆÄÂÜô‰∏∫Ëøô‰∏ÄË°å‰ª£Á†Å
    },
    //ÈÅçÂéÜÂΩìÂâçÁöÑÊâÄÊúâÂÖÉÁ¥†
    each(fn) {
      for (let i = 0; i < elements.length; i++) {
        fn.call(null, elements[i], i);
      }
      return this;
    },
    //Ëé∑ÂèñÊØè‰∏™ÂÖÉÁ¥†ÁöÑÁà∏Áà∏Ôºö
    parent() {
      const array = [];
      this.each(node => {
        if (array.indexOf(node.parentNode) === -1) {
          array.push(node.parentNode);
        }
      });
      //jQueryÂ∞ÅË£Ö‰∏Ä‰∏™ÂØπË±°ÔºåËØ•ÂØπË±°ÂèØ‰ª•Êìç‰ΩúarrayÔºö
      return jQuery(array);
    },
    //Ëé∑ÂèñÂÑøÂ≠êÔºö
    children() {
      const array = [];
      this.each(node => {
        //...node.childrenÁöÑÊÑèÊÄùÊòØÊääËøô‰∏™Êï∞ÁªÑÊãÜÂºÄÔºåÂõ†‰∏∫node.childÂæóÂà∞ÁöÑÊòØ‰∏Ä‰∏™ÊúâÁªìÊûÑÁöÑÊï∞ÁªÑ
        array.push(...node.children);
      });
      return jQuery(array);
    },
    //ÊâìÂç∞Âá∫ÂÖÉÁ¥†Ôºö
    print() {
      console.log(elements);
    },
    //ÁªôËé∑ÂèñÂà∞ÁöÑÂÖÉÁ¥†Ê∑ªÂä†Á±ªclassName:
    addClass(className) {
      //ËØ•ÂÜôÊ≥ïÁ≠â‰∫éÔºö"addClass":function(){}
      //ÈÅçÂéÜËé∑ÂèñÂà∞ÁöÑÂÖÉÁ¥†ÔºåÁªôÊØè‰∏™ÂÖÉÁ¥†Ê∑ªÂä†Á±ªÔºö
      for (let i = 0; i < elements.length; i++) {
        elements[i].classList.add(className);
      } //ËøôÂ∞±ÊòØÈó≠ÂåÖÔºöÂáΩÊï∞addClassËÆøÈóÆ‰∫ÜÂ§ñÈÉ®ÁöÑÂèòÈáèelements
      return this; //thisÂ∞±ÊòØ‰∏äËæπËøîÂõûÁöÑÂØπË±°Ôºõ
      //Áî®Âà∞ÁöÑÁü•ËØÜÔºöÂ¶ÇÊûú‰∏Ä‰∏™ÂØπË±°Ë∞ÉÁî®‰∏Ä‰∏™ÂáΩÊï∞ÔºàÊñπÊ≥ïÔºâÔºåÈÇ£‰πàÂáΩÊï∞ÔºàÂèàÁß∞ÊñπÊ≥ïÔºâÈáåÁöÑthisÂ∞±ÊòØËøô‰∏™ÂØπË±°Ôºõ
      //ÂáΩÊï∞ÔºàÊñπÊ≥ïÔºâÂèØ‰ª•ÈÄöËøáthisËÆøÈóÆË∞ÉÁî®Ëøô‰∏™ÂáΩÊï∞ÔºàÊñπÊ≥ïÔºâÁöÑÂØπË±°„ÄÇ
    },
    oldApi: selectorOrArrayOrTemplate.oldApi,
    //ÁªìÊùüÂΩìÂâçapiÔºåËøîÂõû‰∏ä‰∏Ä‰∏™api
    end() {
      return this.oldApi; //thisÂ∞±ÊòØÂΩìÂâçÁöÑapi//api2
    }
  };
};
